
 
 

.background-container {
  background-color: #7a7a75;
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
}
.logo {
    color: rgb(8, 8, 8);
    font-size: 40px;
    margin-left: 20px;
  }
  
  
  .pages {
    background-color: #0f0f0f;
    padding: 15px ;
    display: flex;
    justify-content: flex-end; 
   
  }
  
 
 
  .right-links-container {
    display: flex;
  list-style-type: none;
  margin: 0;
  padding: 10px;
  
  
  }
  .right-links-container li {
    margin-left: 20px;
  }
  
  .right-links-container li:first-child {
    margin-left: 0;
  }
  
  .right-links-container li a {
    color: rgb(240, 43, 99);
    text-decoration: none;
    text-decoration: none;
    padding: 8px 12px;
    border-radius: 20px;
    background-color: #b5de4c;
    
  }

  .right-links-container li a:hover {
    background-color: #e1741b;
  }
  
  .container {
   
    
    padding: 0 20px;
    
  }
  
  .login-container {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    padding: 30px;
    margin-top: 50px;
    margin-bottom: 50px;
    border: 0px solid #6449a3;
    border-radius: 10px;
    background-color: #b1632a;
    box-sizing: border-box;
  }
  
  .login-container h2 {
    margin-bottom: 20px;
  }
  
  .login-container label {
    display: block;
    margin-bottom: 20px;
  }
  
  .login-container input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
    border: 2px solid #d75b2a;
    border-radius: 3px;
  }
  
  .login-container button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #2b1c76;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  
  .login-container button:hover {
    background-color: #090a0a;
  }
  
  .login-container .error-message {
    color: rgb(3, 3, 3);
    margin-top: 10px;
  }
  
  .app {
    background-color: #070707;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  